<template>
  <el-button @click="goBack">ËøîÂõûÈÖçÁΩÆÈ°µÈù¢</el-button>
  <span style="margin-left: 20px;"><el-text type="primary">ÈÖçÁΩÆÔºö{{
      this.user.username
    }} ‚Äî‚Äî Êñ∞Â¢ûÂºÄÊú∫‰ªªÂä°</el-text></span>
  <el-divider/>
  <el-form :model="form" label-width="auto" style="max-width: 600px">
    <el-form-item label="ÂºÄÊú∫Êï∞ÈáèÔºö">
      <el-input-number v-model="form.createNumbers" :min="1" :max="99999">
        <template #suffix>
          <span>Âè∞</span>
        </template>
      </el-input-number>
    </el-form-item>
    <el-form-item label="Êó∂Èó¥Èó¥ÈöîÔºö">
      <el-input-number v-model="form.interval" :min="1" :max="99999">
        <template #suffix>
          <span>Áßí</span>
        </template>
      </el-input-number>
    </el-form-item>
    <el-form-item label="CPUÔºö">
      <el-input-number v-model="form.ocpus" :min="1" :max="99999">
        <template #suffix>
          <span>Ê†∏</span>
        </template>
      </el-input-number>
    </el-form-item>
    <el-form-item label="ÂÜÖÂ≠òÔºö">
      <el-input-number v-model="form.memory" :min="1" :max="99999">
        <template #suffix>
          <span>GB</span>
        </template>
      </el-input-number>
    </el-form-item>
    <el-form-item label="Á£ÅÁõòÁ©∫Èó¥Ôºö">
      <el-input-number v-model="form.disk" :min="50" :max="99999">
        <template #suffix>
          <span>GB</span>
        </template>
      </el-input-number>
    </el-form-item>
    <el-form-item label="Á≥ªÁªüÊû∂ÊûÑÔºö">
      <div style="display: flex; gap: 10px; align-items: center;">
        <!-- ÈÄâÊã©Ê®°ÂºèÔºöÈ¢ÑËÆæ / Ëá™ÂÆö‰πâ -->
        <el-select v-model="architectureMode" placeholder="ËØ∑ÈÄâÊã©Êû∂ÊûÑ" style="width: 200px;">
          <el-option label="ARM" value="ARM"></el-option>
          <el-option label="AMD" value="AMD"></el-option>
          <el-option label="AMD_E5Ôºà‰ªòË¥πüí∞Ôºâ" value="AMD_E5"></el-option>
          <el-option label="Ëá™ÂÆö‰πâShape" value="custom"></el-option>
        </el-select>

        <!-- ÂΩìÈÄâÊã©Ëá™ÂÆö‰πâÊó∂ÔºåÊòæÁ§∫ËæìÂÖ•Ê°Ü -->
        <el-input
            v-if="architectureMode === 'custom'"
            v-model="form.architecture"
            placeholder="ËØ∑ËæìÂÖ•Ëá™ÂÆö‰πâShapeÔºå‰æãÔºöVM.Standard3.Flex"
            style="width: 400px;"
            clearable
        />
      </div>
    </el-form-item>
    <el-form-item label="Á≥ªÁªüÁ±ªÂûãÔºö">
      <el-radio-group v-model="form.operationSystem">
        <el-radio value="Canonical Ubuntu">Canonical Ubuntu</el-radio>
        <el-radio value="Oracle Autonomous Linux">Oracle Autonomous Linux</el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="rootÂØÜÁ†ÅÔºö" style="width: 70%">
      <el-input placeholder="ËØ∑ËæìÂÖ•rootÂØÜÁ†Å" v-model="form.rootPassword" clearable>
        <template #append>
          <el-button type="text" @click="generatePassword" :icon="Search">ÈöèÊú∫ÁîüÊàê</el-button>
        </template>
      </el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit" :loading="loading">ÂºÄÊú∫</el-button>
      <el-button @click="goBack">ÂèñÊ∂à</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import {ElButton, ElMessage} from 'element-plus';

export default {
  components: {
    ElButton
  },
  data() {
    return {
      loading: false,
      user: {},
      architectureMode: 'ARM', // ÈªòËÆ§ ARM
      form: {
        userId: '',
        ocpus: 1,
        memory: 1,
        disk: 50,
        architecture: 'ARM',
        interval: 60,
        createNumbers: 1,
        operationSystem: 'Canonical Ubuntu',
        rootPassword: '',
      }
    }
  },
  watch: {
    architectureMode(newVal) {
      if (newVal !== 'custom') {
        this.form.architecture = newVal;
      } else {
        this.form.architecture = '';
      }
    }
  },
  mounted() {
    this.user = JSON.parse(this.$route.query.row || '{}')
    this.form.userId = this.user.id
  },
  methods: {
    goBack() {
      this.$router.back(); // ËøîÂõû‰∏ä‰∏Ä‰∏™È°µÈù¢
    },
    generatePassword() {
      const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      let password = '';
      for (let i = 0; i < 8; i++) {
        password += chars.charAt(Math.floor(Math.random() * chars.length));
      }
      this.form.rootPassword = password;
    },
    onSubmit() {
      this.loading = true;
      this.$axios.post('/oci/createInstance', {...this.form})
          .then(response => {
            if (response.data.success) {
              this.loading = false;
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // ËÆæÁΩÆÊèêÁ§∫ÊòæÁ§∫Êó∂Èó¥ÔºåÂçï‰ΩçÊòØÊØ´Áßí
              });
              this.goBack()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // ËÆæÁΩÆÊèêÁ§∫ÊòæÁ§∫Êó∂Èó¥ÔºåÂçï‰ΩçÊòØÊØ´Áßí
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    }
  }
}
</script>

